import React, { useState, useRef, useEffect } from 'react';
import { Box, Grid, TextField } from '@mui/material';

const ColorPicker = ({ value, onChange }) => {
  const [isDragging, setIsDragging] = useState(false);
  const sliderRef = useRef(null);

  const handleHexChange = (color, newValue) => {
    const val = Math.min(255, Math.max(0, parseInt(newValue || 0, 10)));
    onChange({ ...value, [color]: val });
  };

  // Convert hue (0-360) to RGB
  const hueToRgb = (hue) => {
    const h = hue / 60;
    const c = 255;
    const x = c * (1 - Math.abs((h % 2) - 1));

    let r = 0, g = 0, b = 0;
    if (h >= 0 && h < 1) { r = c; g = x; b = 0; }
    else if (h >= 1 && h < 2) { r = x; g = c; b = 0; }
    else if (h >= 2 && h < 3) { r = 0; g = c; b = x; }
    else if (h >= 3 && h < 4) { r = 0; g = x; b = c; }
    else if (h >= 4 && h < 5) { r = x; g = 0; b = c; }
    else if (h >= 5 && h <= 6) { r = c; g = 0; b = x; }

    return { r: Math.round(r), g: Math.round(g), b: Math.round(b) };
  };

  // Convert RGB to hue (0-360)
  const rgbToHue = ({ r, g, b }) => {
    const max = Math.max(r, g, b);
    const min = Math.min(r, g, b);
    if (max === min) return 0;

    let hue = 0;
    if (max === r) {
      hue = (g - b) / (max - min);
    } else if (max === g) {
      hue = 2 + (b - r) / (max - min);
    } else {
      hue = 4 + (r - g) / (max - min);
    }

    hue *= 60;
    if (hue < 0) hue += 360;
    return Math.round(hue);
  };

  const handleMouseDown = (e) => {
    setIsDragging(true);
    handleMouseMove(e);
  };

  const handleMouseMove = (e) => {
    if (!isDragging && e.type !== 'mousedown') return;

    const slider = sliderRef.current;
    const rect = slider.getBoundingClientRect();
    const x = Math.max(0, Math.min(e.clientX - rect.left, rect.width));
    const hue = (x / rect.width) * 360;
    onChange(hueToRgb(hue));
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  useEffect(() => {
    if (isDragging) {
      window.addEventListener('mousemove', handleMouseMove);
      window.addEventListener('mouseup', handleMouseUp);
      return () => {
        window.removeEventListener('mousemove', handleMouseMove);
        window.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [isDragging]);

  const currentHue = rgbToHue(value);
  const position = `${(currentHue / 360) * 100}%`;

  return (
    <Box sx={{ width: '100%' }}>
      <Box
        ref={sliderRef}
        onMouseDown={handleMouseDown}
        sx={{
        width: '100%',
        height: '20px',
        background: 'linear-gradient(to right, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 67%, #ff00ff 83%, #ff0000 100%)',
        borderRadius: '10px',
        cursor: 'pointer',
        position: 'relative',
        '&::after': {
          content: '""',
          position: 'absolute',
          left: position,
          top: '50%',
          transform: 'translate(-50%, -50%)',
          width: '26px',
          height: '26px',
          borderRadius: '50%',
          border: '3px solid white',
          boxShadow: '0 0 4px rgba(0,0,0,0.5)',
          backgroundColor: `rgb(${value.r}, ${value.g}, ${value.b})`,
          cursor: 'pointer',
        }
      }}
    />
      <Grid container spacing={2} sx={{ mt: 2 }}>
        <Grid item xs={4}>
          <TextField
            fullWidth
            size="small"
            style={{ fontSize: 8, lineHeight: 1.5, padding: 0, minHeight: 28  }}
            label="R"
            type="number"
            value={value.r}
            onChange={(e) => handleHexChange('r', e.target.value)}
            inputProps={{ min: 0, max: 255 }}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField
            fullWidth
            size="small"
            style={{ fontSize: 8, lineHeight: 1.5, padding: 0, minHeight: 28  }}
            label="G"
            type="number"
            value={value.g}
            onChange={(e) => handleHexChange('g', e.target.value)}
            inputProps={{ min: 0, max: 255 }}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField
            fullWidth
            size="small"
            style={{ fontSize: 8, lineHeight: 1.5, padding: 0, minHeight: 28  }}
            label="B"
            type="number"
            value={value.b}
            onChange={(e) => handleHexChange('b', e.target.value)}
            inputProps={{ min: 0, max: 255 }}
          />
        </Grid>
      </Grid>
    </Box>
  );
};

export default ColorPicker;