import React from 'react';
import { Menu, MenuItem, Button, Tooltip } from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import FileUploadIcon from '@mui/icons-material/FileUpload';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import TableViewIcon from '@mui/icons-material/TableView';
import CodeIcon from '@mui/icons-material/Code';

export default function MidiDataMenu({ onReset, onSave, onExport, onLoad, onViewTable, onViewVariables }) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenuItemClick = (action) => {
    handleClose();
    action();
  };

  return (
    <div>
      <Tooltip title="Export or import all foot settings.">
        <Button
          variant="text"
          onClick={handleClick}
          startIcon={<SaveIcon />}
          sx={{ color: '#666' }}
        >
          Foot Config
        </Button>
      </Tooltip>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        <MenuItem onClick={() => handleMenuItemClick(onReset)} sx={{ gap: 1 }}>
          <RestartAltIcon fontSize="small" />
          Reset Foot Config
        </MenuItem>
        {/* <MenuItem onClick={() => handleMenuItemClick(onSave)} sx={{ gap: 1 }}>
          <SaveIcon fontSize="small" />
          Save MIDI Data
        </MenuItem> */}
        <MenuItem onClick={() => handleMenuItemClick(onExport)} sx={{ gap: 1 }}>
          <FileDownloadIcon fontSize="small" />
          Export as JSON
        </MenuItem>
        <MenuItem onClick={() => handleMenuItemClick(onLoad)} sx={{ gap: 1 }}>
          <FileUploadIcon fontSize="small" />
          Load JSON
        </MenuItem>
        <MenuItem onClick={() => handleMenuItemClick(onViewTable)} sx={{ gap: 1 }}>
          <TableViewIcon fontSize="small" />
          View as Config Table
        </MenuItem>
        <MenuItem onClick={() => handleMenuItemClick(onViewVariables)} sx={{ gap: 1 }}>
          <CodeIcon fontSize="small" />
          View as Variables
        </MenuItem>
      </Menu>
    </div>
  );
}