import * as React from 'react';
import { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import ContentPasteIcon from '@mui/icons-material/ContentPaste';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import Snackbar from '@mui/material/Snackbar';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { Divider, Box, Grid } from '@mui/material';
import ColorPicker from './ColorPicker';

export default function FormDialog({
  open,
  onCloseFormDialog,
  onSaveFormDialog,
  onChangeFootData,
  selectedBank,
  selectedFootIndex,
  selectedFootData,
}) {

  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');

  const handleCopy = (value, fieldName) => {
    navigator.clipboard.writeText(value).then(() => {
      setSnackbarMessage(`${fieldName} copied to clipboard`);
      setSnackbarOpen(true);
    });
  };

  const handlePaste = async (fieldName, valueType) => {
    try {
      const text = await navigator.clipboard.readText();
      let parsedValue = text;

      if (valueType === 'ledColor') {
        const rgbMatch = text.match(/RGB\((\d+),\s*(\d+),\s*(\d+)\)/);
        if (rgbMatch) {
          parsedValue = {
            r: parseInt(rgbMatch[1]),
            g: parseInt(rgbMatch[2]),
            b: parseInt(rgbMatch[3])
          };
        } else {
          throw new Error('Invalid LED color format');
        }
      } else if (valueType === 'number') {
        parsedValue = parseInt(text);
        if (isNaN(parsedValue)) {
          throw new Error('Invalid number format');
        }
      }

      onChangeFootData(fieldName, parsedValue);
      setSnackbarMessage(`${fieldName} pasted from clipboard`);
      setSnackbarOpen(true);
    } catch (error) {
      setSnackbarMessage(`Failed to paste ${fieldName}: ${error.message}`);
      setSnackbarOpen(true);
    }
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  return (
    <React.Fragment>
      <Dialog
        onClose={onCloseFormDialog}
        aria-labelledby="customized-dialog-title"
        open={open}
        fullWidth={true}
        maxWidth={false}
        sx={{
          '& .MuiDialog-paper': {
            width: '95vw',
            margin: 2
          }
        }}
      >
        <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
          Bank {selectedBank} / Foot {selectedFootIndex}
        </DialogTitle>
        <IconButton
          aria-label="close"
          onClick={onCloseFormDialog}
          sx={(theme) => ({
            position: 'absolute',
            right: 8,
            top: 8,
            color: theme.palette.grey[500],
          })}
        >
          <CloseIcon />
        </IconButton>


          {/* ------- NAME AND BEHAVIOR ----------- */}
        <DialogContent>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <TextField
                autoFocus
                required
                margin="dense"
                id="name"
                name="name"
                label="Name"
                type="text"
                value={selectedFootData.name}
                fullWidth
                variant="outlined"
                onChange={(event) => onChangeFootData("name", event.target.value)}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth variant="outlined" sx={{ mt: 1 }}>
                <InputLabel>Behavior</InputLabel>
                <Select
                  value={selectedFootData.behavior || "press"}
                  onChange={(event) => onChangeFootData("behavior", event.target.value)}
                >
                  <MenuItem value="press">Press</MenuItem>
                  <MenuItem value="stomp">Stomp</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
          
          {/* ------- LED COLOR ----------- */}
          <Divider sx={{ paddingTop:2 }} />
          <Box sx={{ mt: 2 }}>
            <Typography variant="subtitle1" gutterBottom>LED Color</Typography>
            <Box sx={{ mb: 2 }}>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <ColorPicker
                  value={selectedFootData.ledColor}
                  onChange={(color) => onChangeFootData("ledColor", color)}
                />
                <Box sx={{ display: 'flex', flexDirection: 'column', ml: 1 }}>
                  <Tooltip title="Copy LED Color">
                    <IconButton
                      size="small"
                      onClick={() => handleCopy(`RGB(${selectedFootData.ledColor.r}, ${selectedFootData.ledColor.g}, ${selectedFootData.ledColor.b})`, 'LED Color')}
                    >
                      <ContentCopyIcon fontSize="small" />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="Paste LED Color">
                    <IconButton
                      size="small"
                      onClick={() => handlePaste('ledColor', 'ledColor')}
                    >
                      <ContentPasteIcon fontSize="small" />
                    </IconButton>
                  </Tooltip>
                </Box>
              </Box>
            </Box>
            {/* <Grid container spacing={1}>
              {PREDEFINED_COLORS.map((color, index) => (
                <Grid item key={index}>
                  <Button
                    sx={{
                      minWidth: '40px',
                      height: '40px',
                      backgroundColor: rgbToHex(color),
                      border: selectedFootData.ledColor.r === color.r && 
                              selectedFootData.ledColor.g === color.g && 
                              selectedFootData.ledColor.b === color.b 
                              ? '3px solid white' : '1px solid grey',
                      '&:hover': {
                        backgroundColor: rgbToHex(color),
                        opacity: 0.8
                      }
                    }}
                    onClick={() => onChangeFootData("ledColor", color)}
                  />
                </Grid>
              ))}
            </Grid> */}
          </Box>
          <Divider sx={{ paddingTop:2 }} />
          <TableContainer>
            <Table size="medium">
              <TableHead>
                <TableRow>
                  <TableCell>Command</TableCell>
                  <TableCell>Action</TableCell>
                  <TableCell>Channel</TableCell>
                  <TableCell>Type</TableCell>
                  <TableCell>
                      Parameters
                    </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {selectedFootData.footActions.map((footAction, key) => (
                  <TableRow key={key}>
                    <TableCell>
                      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        {key + 1}
                        <Tooltip title="Copy/Paste Command Line">
                          <Box>
                            <IconButton
                              size="small"
                              onClick={() => handleCopy(JSON.stringify(footAction), `Command ${key + 1}`)}
                            >
                              <ContentCopyIcon fontSize="small" />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => {
                                try {
                                  const clipboardText = navigator.clipboard.readText();
                                  clipboardText.then(text => {
                                    const parsedAction = JSON.parse(text);
                                    const updatedFootActions = [...selectedFootData.footActions];
                                    updatedFootActions[key] = parsedAction;
                                    onChangeFootData("footActions", updatedFootActions);
                                    setSnackbarMessage(`Command ${key + 1} pasted successfully`);
                                    setSnackbarOpen(true);
                                  }).catch(error => {
                                    setSnackbarMessage(`Failed to paste command: Invalid format`);
                                    setSnackbarOpen(true);
                                  });
                                } catch (error) {
                                  setSnackbarMessage(`Failed to paste command: ${error.message}`);
                                  setSnackbarOpen(true);
                                }
                              }}
                            >
                              <ContentPasteIcon fontSize="small" />
                            </IconButton>
                          </Box>
                        </Tooltip>
                      </Box>
                    </TableCell>
                    <TableCell>
                      <FormControl fullWidth variant="outlined">
                        <Select
                          size='small'
                          style={{ fontSize: 13.5, lineHeight: 1.5, padding: 0, minHeight: 28  }}
                          value={footAction.footAction}
                          onChange={(event) => {
                            const updatedFootAction = {
                              ...footAction,
                              footAction: event.target.value,
                              midiChannel: event.target.value === "noAction" ? 1 : footAction.midiChannel,
                              midiCommandTypeName: event.target.value === "noAction" ? "" : footAction.midiCommandTypeName,
                              midiParameter1: event.target.value === "noAction" ? 0 : footAction.midiParameter1,
                              midiParameter2: event.target.value === "noAction" ? 0 : footAction.midiParameter2
                            };
                            const updatedFootActions = [...selectedFootData.footActions];
                            updatedFootActions[key] = updatedFootAction;
                            onChangeFootData("footActions", updatedFootActions);
                          }}
                        >
                          <MenuItem value={"noAction"}>No Action</MenuItem>
                          {selectedFootData?.behavior === 'press' ? [
                            <MenuItem key="press" value={"press"}>Press</MenuItem>,
                            <MenuItem key="longPress" value={"longPress"}>Long Press</MenuItem>
                          ] : selectedFootData?.behavior === 'stomp' ? [
                            <MenuItem key="activated" value={"activated"}>Activated</MenuItem>,
                            <MenuItem key="deactivated" value={"deactivated"}>Deactivated</MenuItem>
                          ] : null}
                        </Select>
                      </FormControl>
                    </TableCell>
                    <TableCell>
                      <FormControl fullWidth variant="outlined">
                        <Select
                          size='small'
                          style={{ fontSize: 13.5, lineHeight: 1.5, padding: 0, minHeight: 28  }}
                          value={footAction.midiChannel}
                          onChange={(event) => {
                            const updatedFootAction = { ...footAction, midiChannel: event.target.value };
                            const updatedFootActions = [...selectedFootData.footActions];
                            updatedFootActions[key] = updatedFootAction;
                            onChangeFootData("footActions", updatedFootActions);
                          }}
                          disabled={footAction.footAction === "noAction"}
                        >
                          {[...Array(16)].map((_, i) => (
                            <MenuItem key={i + 1} value={i + 1}>Channel {i + 1}</MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </TableCell>
                    <TableCell>
                      <FormControl fullWidth variant="outlined">
                        <Select
                          size='small'
                          style={{ fontSize: 13.5, lineHeight: 1.5, padding: 0, minHeight: 28  }}
                          value={footAction.midiCommandTypeName}
                          onChange={(event) => {
                            const updatedFootAction = { ...footAction, midiCommandTypeName: event.target.value };
                            const updatedFootActions = [...selectedFootData.footActions];
                            updatedFootActions[key] = updatedFootAction;
                            onChangeFootData("footActions", updatedFootActions);
                          }}
                          disabled={footAction.footAction === "noAction"}
                        >
                          <MenuItem value={"ProgramChange"}>Program Change</MenuItem>
                          <MenuItem value={"ControlChange"}>Control Change</MenuItem>
                        </Select>
                      </FormControl>
                    </TableCell>
                    <TableCell>
                      <Grid container spacing={1}>
                        <Grid item xs={6}>
                          <TextField
                            fullWidth
                            size="small"
                            type="number"
                            variant="outlined"
                            value={footAction.midiParameter1}
                            onChange={(event) => {
                              const updatedFootAction = { ...footAction, midiParameter1: parseInt(event.target.value) };
                              const updatedFootActions = [...selectedFootData.footActions];
                              updatedFootActions[key] = updatedFootAction;
                              onChangeFootData("footActions", updatedFootActions);
                            }}
                            disabled={footAction.footAction === "noAction"}
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <TextField
                            fullWidth
                            size="small"
                            type="number"
                            variant="outlined"
                            value={footAction.midiParameter2}
                            onChange={(event) => {
                              const updatedFootAction = { ...footAction, midiParameter2: parseInt(event.target.value) };
                              const updatedFootActions = [...selectedFootData.footActions];
                              updatedFootActions[key] = updatedFootAction;
                              onChangeFootData("footActions", updatedFootActions);
                            }}
                            disabled={footAction.footAction === "noAction" || footAction.midiCommandTypeName === "ProgramChange"}
                          />
                        </Grid>
                      </Grid>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </DialogContent>

        <DialogActions>
          <Button onClick={onCloseFormDialog}>Cancel</Button>
          <Button onClick={() => onSaveFormDialog(selectedBank, selectedFootIndex, selectedFootData)} variant='outlined' type="submit">Save</Button>
        </DialogActions>
      </Dialog>


      <Snackbar
        open={snackbarOpen}
        autoHideDuration={2000}
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      />
    </React.Fragment>
  );
}
