import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function MidiDataTable({ open, onClose, midiData }) {
  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="lg"
      fullWidth
    >
      <DialogTitle>MIDI Data Table</DialogTitle>
      <DialogContent>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                {/* <TableCell>Bank</TableCell> */}
                <TableCell>Foot Index</TableCell>
                <TableCell>Name</TableCell>
                <TableCell>LED Color</TableCell>
                <TableCell>Behavior</TableCell>
                <TableCell>Foot Action</TableCell>
                <TableCell>MIDI Channel</TableCell>
                <TableCell>Command Type</TableCell>
                <TableCell>Parameter 1</TableCell>
                <TableCell>Parameter 2</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {midiData.filter(foot => foot.bank === 1).map((foot) => (
                foot.footActions.map((action, actionIndex) => (
                  <TableRow key={`${foot.footIndex}-${actionIndex}`}>
                    {/* <TableCell>{foot.bank}</TableCell> */}
                    <TableCell>{foot.footIndex}</TableCell>
                    <TableCell>{foot.name}</TableCell>
                    <TableCell>{foot.ledColor ? `RGB(${foot.ledColor.r}, ${foot.ledColor.g}, ${foot.ledColor.b})` : ''}</TableCell>
                    <TableCell>{foot.behavior}</TableCell>
                    <TableCell>{action.footAction}</TableCell>
                    <TableCell>{action.midiChannel}</TableCell>
                    <TableCell>{action.midiCommandTypeName}</TableCell>
                    <TableCell>{action.midiParameter1}</TableCell>
                    <TableCell>{action.midiParameter2}</TableCell>
                  </TableRow>
                ))
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
}