// BrowserCompatibility.js

/**
 * Utility class to check browser compatibility for various features
 * used in the MIDI Controller Web Editor
 */
class BrowserCompatibility {
  /**
   * Check if Web Serial API is supported in the current browser
   * @returns {boolean} True if Web Serial API is supported
   */
  static isWebSerialSupported() {
    return 'serial' in navigator;
  }

  /**
   * Check if Web MIDI API is supported in the current browser
   * @returns {boolean} True if Web MIDI API is supported
   */
  static isWebMidiSupported() {
    return 'requestMIDIAccess' in navigator;
  }

  /**
   * Get browser compatibility status for all required features
   * @returns {Object} Object containing compatibility status for each feature
   */
  static getCompatibilityStatus() {
    return {
      webSerial: this.isWebSerialSupported(),
      webMidi: this.isWebMidiSupported(),
      isFullyCompatible: this.isWebSerialSupported() && this.isWebMidiSupported()
    };
  }

  /**
   * Get browser compatibility message based on current browser support
   * @returns {Object} Object containing message and severity
   */
  static getCompatibilityMessage() {
    const status = this.getCompatibilityStatus();
    
    if (status.isFullyCompatible) {
      return {
        message: 'Your browser fully supports all features.',
        severity: 'success'
      };
    }
    
    if (!status.webSerial && !status.webMidi) {
      return {
        message: 'Your browser does not support Web Serial API and Web MIDI API. Please use Chrome, Edge, or Opera for full functionality.',
        severity: 'error'
      };
    }
    
    if (!status.webSerial) {
      return {
        message: 'Your browser does not support Web Serial API. Device connection will not work. Please use Chrome, Edge, or Opera for full functionality.',
        severity: 'warning'
      };
    }
    
    if (!status.webMidi) {
      return {
        message: 'Your browser does not support Web MIDI API. Live mode will not work. Please use Chrome, Edge, or Opera for full functionality.',
        severity: 'warning'
      };
    }
    
    return {
      message: 'Unknown compatibility issue.',
      severity: 'error'
    };
  }
}

export default BrowserCompatibility;