import React, { useState, useEffect } from 'react';
import { Button, Box, Alert, Tooltip, Switch, CircularProgress } from '@mui/material';
import BrowserCompatibility from '../utils/BrowserCompatibility';
import SerialCommunication from '../utils/SerialCommunication';
import SaveIcon from '@mui/icons-material/Save';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

export default function SerialConnection({ midiData, onConnectionChange, setMidiData }) {
  const [isConnected, setIsConnected] = useState(false);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isSerialSupported, setIsSerialSupported] = useState(true);
  
  useEffect(() => {
    // Check if Web Serial API is supported when component mounts
    const supported = BrowserCompatibility.isWebSerialSupported();
    setIsSerialSupported(supported);
    if (!supported) {
      setError('Web Serial API is not supported in this browser. Device connection will not work. Please use Chrome, Edge, or Opera.');
    }
  }, []);

  const handleConnect = async () => {
    console.log('Initializing serial connection...');
    setIsLoading(true);
    try {
      const connected = await SerialCommunication.connect();
      setIsConnected(connected);
      setError(null);
      if (onConnectionChange) {
        onConnectionChange(connected);
      }
    } catch (err) {
      setError('Failed to connect to Midi Controller: ' + err.message);
      setIsConnected(false);
    } finally {
      setTimeout(() => {
        setIsLoading(false);
      }, 3000);
    }
  };

  const handleDisconnect = async () => {
    setIsLoading(true);
    try {
      await SerialCommunication.disconnect();
      setIsConnected(false);
      setError(null);
      if (onConnectionChange) {
        onConnectionChange(false);
      }
    } catch (err) {
      setError('Error disconnecting from Midi Controller: ' + err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSendToArduino = async () => {
    try {
      SerialCommunication.onLoadingStateChange = setIsLoading;
      const success = await SerialCommunication.sendSerialData(midiData);
      if (!success) {
        setError('Failed to send data to Midi Controller');
      }
    } catch (err) {
      setError('Error sending data to Midi Controller: ' + err.message);
      setIsLoading(false);
    }
  };

  const handleCopyConfig = async () => {
    try {
      SerialCommunication.onLoadingStateChange = setIsLoading;
      const config = await SerialCommunication.copyConfig();
      console.log('Config copied:', config);
      if (config) {
        setError(null);
        // Transform the received config into midiData format
        const updatedMidiData = midiData.map(foot => {
          if (foot.bank === 1) {
            const footswitch = config.footswitches[foot.footIndex - 1];
            return {
              ...foot,
              ledColor: footswitch.ledColor,
              name: footswitch.name,
              behavior: footswitch.behaviorMode,
              footActions: footswitch.footActions.map(action => ({
                footAction: action.footAction,
                midiChannel: action.midiChannel,
                midiCommandTypeName: action.midiType,
                midiParameter1: action.midiParameter1,
                midiParameter2: action.midiParameter2
              }))
            };
          }
          return foot;
        });
        if (setMidiData) {
          setMidiData(updatedMidiData);
        }
      } else {
        setError('Failed to copy config from Midi Controller');
      }
    } catch (err) {
      setError('Error copying config from Midi Controller: ' + err.message);
      setIsLoading(false);
    }
  };

  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
      <Tooltip title={isSerialSupported ? "Midi controller device connection" : "Web Serial API is not supported in this browser"}>
        <span>
          <Button
            variant="text"
            color={isConnected ? 'success' : 'inherit'}
            onClick={isConnected ? handleDisconnect : handleConnect}
            startIcon={<Switch checked={isConnected} size="small" />}
            sx={{ color: isConnected ? undefined : '#666' }}
            disabled={isLoading || !isSerialSupported}
          >
            {isConnected ? 'Connected' : 'Connect to Device'}
          </Button>
        </span>
      </Tooltip>

      {isConnected && (
        <>
        {isLoading && <CircularProgress size={16} sx={{ ml: 1, color: 'white' }} />}
        <Tooltip title="Copy configuration from Midi Controller device.">
            <Button
              variant="text"
              color="inherit"
              startIcon={<ContentCopyIcon />}
              onClick={handleCopyConfig}
              sx={{ color: 'white' }}
              disabled={isLoading}
            >
              Get Config
            </Button>
          </Tooltip>
          <Tooltip title="Save midi commands configuration do Midi Controller device.">
            <Button
              variant="text"
              color="inherit"
              startIcon={<SaveIcon />}
              onClick={handleSendToArduino}
              sx={{ color: 'white' }}
              disabled={isLoading}
            >
              Save Config
            </Button>
          </Tooltip>
        </>)}

      {error && (
        <Alert severity="error" sx={{ py: 0, px: 1 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
}