// SerialCommunication.js
import BrowserCompatibility from './BrowserCompatibility';

class SerialCommunication {
  constructor() {
    this.port = null;
    this.reader = null;
    this.writer = null;
    this.isConnected = false;
    this.encoder = new TextEncoder();
    this.decoder = new TextDecoder();
    this.messageBuffer = '';
    this.onSaveConfigResponse = null;
    this.onCopyConfigResponse = null;
    this.isLoading = false;
    this.onLoadingStateChange = null;
    this.configBuffer = {
      footswitches: Array(8).fill().map(() => ({
        ledColor: { r: 0, g: 0, b: 0 },
        name: '',
        behaviorMode: '',
        footActions: []
      }))
    };
  }

  async connect() {
    try {
      console.log('Initializing serial connection...');
      
      // Check if Web Serial API is supported
      if (!BrowserCompatibility.isWebSerialSupported()) {
        throw new Error('Web Serial API is not supported in this browser. Please use Chrome, Edge, or Opera.');
      }
      
      // Request port access with Arduino USB filters
      const filters = [
        { usbVendorId: 0x2341 }, // Arduino SA
        { usbVendorId: 0x1A86 }, // QinHeng Electronics (CH340 chip commonly used in Arduino clones)
        { usbVendorId: 0x2A03 }  // Arduino LLC (some models)
      ];
      
      this.port = await navigator.serial.requestPort({ filters });
      await this.port.open({ baudRate: 9600 }); // Match Arduino baud rate
      
      // Create reader and writer
      this.reader = this.port.readable.getReader();
      this.writer = this.port.writable.getWriter();
      
      // Start reading loop
      this.startReading();
      
      this.isConnected = true;
      console.log('✅ Serial connection successfully established');
      return true;
    } catch (error) {
      console.error('❌ Failed to establish serial connection:', error.message);
      return false;
    }
  }

  async disconnect() {
    try {
      if (this.reader) {
        await this.reader.cancel();
        await this.reader.releaseLock();
      }
      if (this.writer) {
        await this.writer.releaseLock();
      }
      if (this.port) {
        await this.port.close();
      }
      this.isConnected = false;
      console.log('✅ Serial connection successfully closed');
    } catch (error) {
      console.error('❌ Error during serial port disconnection:', error.message);
    }
  }

  async startReading() {
    while (true) {
      try {
        const { value, done } = await this.reader.read();
        if (done) {
          break;
        }
        
        // Append new data to buffer
        this.messageBuffer += this.decoder.decode(value);
        
        // Process complete messages
        while (this.messageBuffer.includes('\n')) {
          const newlineIndex = this.messageBuffer.indexOf('\n');
          const message = this.messageBuffer.slice(0, newlineIndex);
          this.messageBuffer = this.messageBuffer.slice(newlineIndex + 1);
          
          if (message.trim()) {
            try {
              const data = JSON.parse(message);
              console.log('📥 Received data from Midi Controller:', data);
              
              // Handle responses
              if (data.status === 'success') {
                if (data.action === 'saveBank' && this.onSaveConfigResponse) {
                  this.onSaveConfigResponse(data);
                  this.onSaveConfigResponse = null;
                } else if (data.action === 'getConfig' && this.onCopyConfigResponse) {
                  this.onCopyConfigResponse(this.configBuffer);
                  this.onCopyConfigResponse = null;
                  this.configBuffer = {
                    footswitches: Array(8).fill().map(() => ({
                      ledColor: { r: 0, g: 0, b: 0 },
                      name: '',
                      behaviorMode: '',
                      footActions: []
                    }))
                  };
                }
              }
            } catch (e) {
              console.log('📥 Received raw data from Midi Controller:', message);
              
              // Process configuration messages
              const [command, ...params] = message.split(',').map(p => p.replace(/["\n\r]/g, ''));
              const footIndex = parseInt(params[0]);
              
              if (footIndex >= 0 && footIndex < 8) {
                switch(command) {
                  case 'L':
                    this.configBuffer.footswitches[footIndex].ledColor = {
                      r: parseInt(params[1]),
                      g: parseInt(params[2]),
                      b: parseInt(params[3])
                    };
                    break;
                  case 'N':
                    this.configBuffer.footswitches[footIndex].name = params[1];
                    break;
                  case 'H':
                    this.configBuffer.footswitches[footIndex].behaviorMode = params[1];
                    break;
                  case 'C':
                    const actionIndex = parseInt(params[1]);
                    if (actionIndex >= 0 && actionIndex < 2) {
                      const footAction = {
                        footAction: params[2],
                        midiChannel: parseInt(params[3]),
                        midiType: this.getMidiTypeFromCode(params[4]),
                        midiParameter1: parseInt(params[5]),
                        midiParameter2: parseInt(params[6])
                      };
                      
                      if (!this.configBuffer.footswitches[footIndex].footActions[actionIndex]) {
                        this.configBuffer.footswitches[footIndex].footActions[actionIndex] = footAction;
                      }
                    }
                    break;
                }
              }
            }
          }
        }
      } catch (error) {
        console.error('Error reading data:', error);
        break;
      }
    }
  }

  async copyConfig() {
    if (!this.writer || !this.isConnected) {
      console.error('❌ Cannot copy config: No serial connection available');
      return false;
    }

    try {
      this.setLoadingState(true);
      console.log('📥 Requesting config from MIDI Controller...');
      
      return new Promise((resolve) => {
        this.onCopyConfigResponse = (response) => {
          console.log('✅ Config successfully copied from MIDI Controller');
          this.setLoadingState(false);
          resolve(response);
        };
        
        this.sendCommand('G').catch(error => {
          console.error('❌ Failed to send copy command:', error.message);
          this.onCopyConfigResponse = null;
          this.setLoadingState(false);
          resolve(null);
        });
      });
    } catch (error) {
      console.error('❌ Failed to copy config:', error.message);
      this.setLoadingState(false);
      return null;
    }
  }

  async sendCommand(command) {

    // inserting delay to avoid overloading the serial port
    await new Promise(resolve => setTimeout(resolve, 500));

    if (!this.writer || !this.isConnected) {
      console.error('❌ Cannot send command: No serial connection available');
      return false;
    }

    try {
      console.log('📤 Sending command to MIDI Controller:', command);
      await this.writer.write(this.encoder.encode(command + '\n'));
      return true;
    } catch (error) {
      console.error('❌ Failed to send command:', error.message);
      return false;
    }
  }

  async sendSerialData(midiData) {
    if (!this.writer || !this.isConnected) {
      console.error('❌ Cannot send serial data: No serial connection available');
      return false;
    }

    try {
      this.setLoadingState(true);
      console.log('📤 Sending serial data to MIDI Controller...');
      const formattedCommands = this.formatSerialDataMidiController(midiData);
      
      for (const command of formattedCommands) {
        await this.sendCommand(command);
      }
      
      // Save configuration to EEPROM and wait for response
      return new Promise((resolve) => {
        this.onSaveConfigResponse = (response) => {
          console.log('✅ MIDI data successfully saved to MIDI Controller');
          this.setLoadingState(false);
          resolve(true);
        };
        
        this.sendCommand('S').catch(error => {
          console.error('❌ Failed to send save command:', error.message);
          this.onSaveConfigResponse = null;
          this.setLoadingState(false);
          resolve(false);
        });
      });
    } catch (error) {
      console.error('❌ Failed to send MIDI data:', error.message);
      return false;
    }
  }

  formatSerialDataMidiController(_midiData) {
    const commands = [];
    const midiData = _midiData.filter(foot => foot.bank === 1);
    
    for (const foot of midiData) {

      // Update LED color
      if (foot.ledColor && foot.ledColor.r !== undefined) {
        const { r, g, b } = foot.ledColor;
        commands.push(`L,${foot.footIndex-1},${r},${g},${b}`);
      }

      // Update name if provided
      if (foot.name) {
        commands.push(`N,${foot.footIndex-1},${foot.name}`);
      }

      // Update behavior mode
      if (foot.behaviorMode) {
        commands.push(`H,${foot.footIndex-1},${foot.behaviorMode}`);
      }

      // Update MIDI commands
      if (foot.footActions && foot.footActions.length > 0) {
        foot.footActions.forEach((action, index) => {
          if (index > 1) return; // Only support 2 commands per foot
          
          const midiType = this.getMidiTypeCode(action.midiType);
          if (midiType === 'NA') return; // Skip invalid MIDI types

          commands.push(
            `C,${foot.footIndex-1},${index},${action.footAction},${action.midiChannel},${midiType},${action.midiParameter1},${action.midiParameter2}`
          );
        });
      }
      
    }

    console.log('Formatted commands:', commands); // Log formatted command
    return commands;
  }

  getMidiTypeCode(midiType) {
    const typeMap = {
      'programChange': 'PC',
      'controlChange': 'CC',
      'noteOn': 'NON',
      'noteOff': 'NOF'
    };
    return typeMap[midiType] || 'NA';
  }

  getMidiTypeFromCode(code) {
    const typeMap = {
      'PC': 'programChange',
      'CC': 'controlChange',
      'NON': 'noteOn',
      'NOF': 'noteOff'
    };
    return typeMap[code] || 'unknown';
  }

  setLoadingState(isLoading) {
    this.isLoading = isLoading;
    if (this.onLoadingStateChange) {
      this.onLoadingStateChange(isLoading);
    }
  }
}

export default new SerialCommunication();