import React, { useState, useEffect } from 'react';
import { Alert, Box, IconButton, Collapse } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import BrowserCompatibility from '../utils/BrowserCompatibility';

/**
 * Component to display browser compatibility warnings
 * Shows a dismissible banner with compatibility information
 */
const BrowserCompatibilityBanner = () => {
  const [open, setOpen] = useState(true);
  const [compatibilityInfo, setCompatibilityInfo] = useState(null);

  useEffect(() => {
    // Get compatibility status on component mount
    const info = BrowserCompatibility.getCompatibilityMessage();
    setCompatibilityInfo(info);
  }, []);

  // Don't render anything if browser is fully compatible
  if (!compatibilityInfo || compatibilityInfo.severity === 'success') {
    return null;
  }

  return (
    <Box sx={{ width: '100%', mb: 2 }}>
      <Collapse in={open}>
        <Alert
          severity={compatibilityInfo.severity}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => setOpen(false)}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
          sx={{ mb: 2 }}
        >
          {compatibilityInfo.message}
        </Alert>
      </Collapse>
    </Box>
  );
};

export default BrowserCompatibilityBanner;