import React, { useState, useEffect } from 'react';
import { Switch, FormControlLabel, Tooltip, Box, Alert, FormControl, InputLabel, Select, MenuItem, Button } from '@mui/material';
import BrowserCompatibility from '../utils/BrowserCompatibility';

export default function MidiLiveMode({ midiData, selectedBank, onLiveModeChange }) {
  const [midiAccess, setMidiAccess] = useState(null);
  const [midiOutput, setMidiOutput] = useState(null);
  const [isLiveMode, setIsLiveMode] = useState(false);
  const [error, setError] = useState(null);

  const sendMidiCommand = React.useCallback((footData, actionType = null) => {
    if (!isLiveMode) return;
    
    // If no MIDI output is available, only handle LED states
    if (!midiOutput) return;

    footData.footActions.forEach(action => {
      
      if (action.footAction === 'noAction') return;
      
      // For stomp behavior, only send commands for the current state
      if (footData.behavior === 'stomp' && actionType &&  action.footAction !== actionType) return;

      // For press behavior, only send commands for 'press' action
      if (footData.behavior === 'press' && !['press','longPress'].includes(action.footAction) ) return;

      const channel = action.midiChannel - 1;
      const commandType = action.midiCommandTypeName;

      switch (commandType) {
        case 'ProgramChange':
          midiOutput.send([0xC0 | channel, action.midiParameter1]);
          break;
        case 'ControlChange':
          midiOutput.send([0xB0 | channel, action.midiParameter1, action.midiParameter2]);
          break;
        // case 'SystemExclusive':
        //   // Convert hex string to array of numbers
        //   const sysExData = action.midiParameter1.split(' ')
        //     .filter(hex => hex.length > 0)
        //     .map(hex => parseInt(hex, 16));
          
        //   // Validate SysEx message format
        //   if (sysExData[0] === 0xF0 && sysExData[sysExData.length - 1] === 0xF7) {
        //     midiOutput.send(sysExData);
        //   } else {
        //     console.warn('Invalid SysEx message format');
        //   }
        //   break;
        default:
          console.warn('Unsupported MIDI command type:', commandType);
      }
    });
  }, [midiOutput, isLiveMode]);

  useEffect(() => {
    // Check if Web MIDI API is supported
    if (BrowserCompatibility.isWebMidiSupported()) {
      navigator.requestMIDIAccess()
        .then(access => {
          setMidiAccess(access);
          // Automatically select the first available output
          const outputs = Array.from(access.outputs.values());
          if (outputs.length > 0) {
            setMidiOutput(outputs[0]);
          }
        })
        .catch(err => setError('Failed to access MIDI devices: ' + err.message));
    } else {
      setError('Web MIDI API is not supported in your browser. Live mode will not work. Please use Chrome, Edge, or Opera for full functionality.');
    }
  }, []);

  useEffect(() => {
    const handleMidiFootClick = (event) => {
      console.log('MidiLiveMode: handleMidiFootClick', event.detail);
      const footData = midiData.find(item => 
        item.bank === selectedBank && item.footIndex === parseInt(event.detail.footIndex)
      );
      if (footData) {
        const actionType = event.detail.actionType;
        const action = footData.footActions.find(action => action.footAction === actionType);
        if (action) {
          sendMidiCommand({ ...footData, footActions: [action] });
        }
      }
    };

    window.addEventListener('midiFootClick', handleMidiFootClick);

    return () => {
      window.removeEventListener('midiFootClick', handleMidiFootClick);
    };
  }, [selectedBank, midiData, sendMidiCommand]);


  const handleMidiOutputChange = (event) => {
    const selectedOutput = Array.from(midiAccess.outputs.values())
      .find(output => output.name === event.target.value);
    setMidiOutput(selectedOutput);
  };

  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
      <Tooltip title="Simulate foot command operation on browser.">
        <Button
          variant="text"
          color={isLiveMode ? "success" : "inherit"}
          onClick={(event) => {
            setIsLiveMode(!isLiveMode);
            onLiveModeChange(!isLiveMode);
          }}
          startIcon={<Switch checked={isLiveMode} size="small" />}
          sx={{ color: isLiveMode ? undefined : '#666' }}
        >
          Live Mode
        </Button>
      </Tooltip>
      
      {midiAccess && Array.from(midiAccess.outputs.values()).length > 0 && (
        <FormControl size="small" sx={{ minWidth: 150 }}>
          <Select
            value={midiOutput?.name || ''}
            onChange={handleMidiOutputChange}
            displayEmpty
            sx={{ height: 36 }}
          >
            <MenuItem disabled value="">
              <em>MIDI Output Port</em>
            </MenuItem>
            {Array.from(midiAccess.outputs.values()).map((output) => (
              <MenuItem key={output.name} value={output.name}>
                {output.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      )}
      {!midiOutput && !error && isLiveMode && (
        <Tooltip title="Connect a MIDI device and refresh the page to control MIDI devices.">
          <Alert severity="warning" sx={{ py: 0, px: 1, marginTop: '-3px'}} />
        </Tooltip>
      )}
      {error && (
        <Alert severity="error" sx={{ py: 0, px: 1 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
}